<template>
  <div id="content">
    <div class="content_title">{{ title }}</div>
    <el-divider></el-divider>
    <div id="title">
      <div id="title_status">
        <div id="title_time">日期：{{year}}-{{month}}-{{day}}</div>
      </div>
      <div id="title_body">
        <template v-for="(item,index) in title_body">
          <h1 v-if="item.type=='标题'" :key="index">{{item.content}}</h1>

          <p v-else-if="item.type=='内容'" :key="index">{{item.content}}</p>

          <blockquote v-else-if="item.type=='重点内容'" :key="index">
            <p>{{item.content}}</p>
          </blockquote>

          <p v-else-if="item.type=='图片'" :key="index">
            <img :src="item.content" alt="图片" />
          </p>
        </template>
      </div>
      <div id="title_bottom">
        <p>（正文完）</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["title", "year", "month", "day"],
  data() {
    return {
      title_body: []
    };
  },
  created() {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData();
  },
  methods: {
    fetchData() {
      // replace getPost with your data fetching util / API wrapper
      var geturl =
        "/blogjson/" +
        this.year +
        "/" +
        this.month +
        "/" +
        this.day +
        "/" +
        this.title +
        ".json";
      this.$http
        .get(geturl)
        .then(response => {
          this.title_body = response.data.title_body;
        })
        .catch(function(error) {
          // eslint-disable-next-line
          console.log(error);
        });
    }
  }
};
</script>
<style lang="less" scoped>
.content_title {
  text-align: center;
}
#title {
  font-size: 1.6em;
  #title_time {
    text-align: right;
  }
  #title_body {
    font-size: 1.6em;
    h1 {
      font-size: 1em;
      border-bottom: 1px solid #ccc;
    }
    p {
      font-size: 0.6em;
      margin-left: 1em;
      margin-top: 1em;
      margin-bottom: 0;
    }
    blockquote {
      background-color: #f5f2f0;
      padding: 0.5em;
      margin: 1em 1em;
      -moz-border-radius: 1em;
      -webkit-border-radius: 1em;
      border-radius: 0.5em;
      font-family: Consolas, Monaco, "Andale Mono", monospace;
      border-width: 0.1em;
      border-color: #e0dfcc;
      border-style: solid;
      overflow: auto;
      p {
        margin-bottom: 0;
        margin-top: 0.6em;
      }
    }
    img {
      border-radius: 0.5em;
      border-width: 0.1em;
      border-color: #e0dfcc;
      border-style: solid;
      max-width: 90%;
    }
  }
}
</style>